;(function() {

  function Animations() {
    var self = this;
    self.init = init
    self.in = animateIn
    self.out = animateOut

    var bottleInTl;
    var bottleOutTl;
    var cupInTl;
    var cupOutTl;
    var carInTl;
    var carOutTl;

    var animations = []
    var current = null;

    function init(){
      createBottleAnimation();
      createCupAnimation();
      createCarAnimation();
    }

    function createBottleAnimation(){
      $('#bottle').hide();
      bottleInTl = new TimelineMax({paused: true});

      bottleInTl.set([$('#bottle_body > *'),$('#cap_top')], {drawSVG:"0%"})
      bottleInTl.set($('#bottle'), {'rotation':"30", transformOrigin:'50% 50%', y:"750px"})
      bottleInTl.set($('#lines > *'), {drawSVG:"0% 0%"})
      bottleInTl.set($('#cap_bottom'), {opacity:0})

      bottleInTl.to($('#bottle'), 1.5,{'rotation':0, y:0, ease:Back.easeOut})
      bottleInTl.to($('#bottle_body > *'), 1,{drawSVG:"100%", ease:Power2.easeOut}, '-=1')
      bottleInTl.to($('#cap_top'), 1,{drawSVG:"100%", ease:Power2.easeOut}, '-=1')

      bottleInTl.to($('#cap_bottom'), .1,{'opacity':1, y:0, ease:Linear.easeNone})
      bottleInTl.to($('#cap'), .3,{y:'-260', x:'150', ease:Power2.easeOut}, '-=.1')
      bottleInTl.to($('#lines > *'), .2,{drawSVG:"40% 90%", ease:Power2.easeOut}, '-=.2')
      bottleInTl.to($('#bottle_body'), 1,{y:'30', x:'-20', ease:Power2.easeOut}, '-=.3')


      bottleOutTl = new TimelineMax({paused: true, onComplete: function(){
        $('#bottle').hide()
      }});

      bottleOutTl.to([$('#bottle_body > *'),$('#cap_top'),$('#cap_bottom'),$('#lines > *')], 1, {drawSVG:"0%", ease:Power2.easeIn})

      animations.push({in: animateBottleIn, out: animateBottleOut})
    }

    function createCupAnimation(){
      /*  Creazione timeline animazione coppa */
      cupTl = new TimelineMax({paused: true});
      // animations.push({in: animateCupIn, out: animateCupOut})
    }

    function createCarAnimation(){
      /*  Creazione timeline animazione macchina */
      carTl = new TimelineMax({paused: true});
      // animations.push({in: animateCarIn, out: animateCarOut})
    }

    function animateBottleIn(){
      $('#bottle').css({'display': 'block'});
      bottleInTl.restart();
    }
    function animateBottleOut(){
      bottleOutTl.restart();
    }

    function animateCupIn(){
      cupInTl.restart();
    }
    function animateCupOut(){
      cupOutTl.restart();
    }

    function animateCarIn(){
      carInTl.restart();
    }
    function animateCarOut(){
      carOutTl.restart();
    }

    function animateIn(){
      current = randElem(animations);
      current.in();
    }

    function animateOut(){
      if(current) current.out();
    }
}

  window.Animations = Animations;
})()