;(function() {

  function ImageGrid() {
    var self = this;
    var cards = []
    var queue = []

    self.create = create;
    self.init = init;
    self.separateCards = separateCards;
    self.resetCards = resetCards;
    self.update = updateCards;
    self.showLoader = showLoader
    self.hideLoader = hideLoader

    var container = null;
    var grid_container = null;
    var featured_container = null;
    var cycle = null;
    var currentShowcase = null;

    function init() {
      showcase_container = $('#showcase-container')
      grid_container = $('#cards-container');
      featured_container = $('#featured-container');
    }

    function create(data) {
      cards = data;
      createGrid(cards)
      cycle = setInterval(showcase, APP.timers.cycle)
    }

    function updateCards(newCards) {
      var nc = _.differenceBy(newCards, queue, cards, function(c) {
        return c.date
      })
      console.log(nc)
      queue = _.concat(queue, nc)
    }

    function createGrid(cards) {
      if(cards.length < 18){
        console.log(cards.length)
        for(var i = cards.length; i <= 18; i++ ){
          var dummy = {
            date: moment().unix()-500*i,
            id: i,
            img_url: "img/placeholder.jpg",
            twitter: Math.round(Math.random()),
            profile_handle: "EnelChampion",
            profile_name: "Jaguar Racing",
            profile_pic: "img/placeholder.jpg"
          }
          cards.push(dummy)
        }
        console.log(cards)
      }

      var featured_cards = _.take(cards, 2);
      var grid_cards = _.difference(cards, featured_cards);

      featured_cards.forEach(function(f) {
        var card = createCard(f);
        featured_container.append(card);
      })

      grid_cards.forEach(function(c) {
        var card = createCard(c)
        grid_container.append(card)
      })
    }

    function updateGrid() {
      var featured_cards = _.take(cards, 2);
      var grid_cards = _.difference(cards, featured_cards);

      featured_cards.forEach(function(f, i) {
        var card = featured_container.children().eq(i)
        card.find('.fe-card-wrapper').transition({
          opacity: 0,
          duration: 200,
          complete: function(e) {
            setCardData(card, f);
            $(this).transition({
              opacity: 1,
              duration: 200
            })
          }
        }, 'easeOutExpo')
      })
      grid_cards.forEach(function(f, i) {
        var card = grid_container.children().eq(i)
        card.find('.fe-card-wrapper').transition({
          opacity: 0,
          duration: 200,
          delay: _.random(100, 300),
          complete: function(e) {
            setCardData(card, f);
            $(this).transition({
              opacity: 1,
              duration: 200
            })
          }
        })
      }, 'easeOutExpo')

    }

    function setCardData(e, c) {
      e.find('.user-name').text(c.profile_handle)
      var time = moment.unix(c.date).fromNow();
      e.find('.fe-card-time').text(time)
      e.find('.user-picture').css('background-image', 'url(' + c.profile_pic + ')')
      var img = e.find('.fe-card-image')
      img.attr('src', c.img_url);
      img.hide();
      e.find('.loader').show()
      img.on("load", function(){
        e.find('.loader').hide()
        img.show();
      })
    }

    function createCard(c) {
      var card = $('<div class="fe-card"></div>')
      var wrap = $('<div class="fe-card-wrapper"></div>')
      var image = $('<div class="fe-image-container"></div>')
      var info = $('<div class="fe-card-info"><div class="fe-user-info"><div class="user-picture"></div><div class="user-name">Display Name</div></div><p class="fe-card-time">12/11/2017</p></div>')
      image.append('<img src="#" class="fe-card-image"></img>')
      wrap.append(image, info);
      card.append(wrap)
      setCardData(card, c)
      card.attr('id', 'card-'+c.id)
      image.append('<div class="loader"></div>');
      if(c.twitter) card.addClass('twitter')
      else card.addClass('instagram')
      return card;
    }

    function separateCards() {
      $('.container:not(.showcase) .fe-card').each(function(i, e) {
        var dir = APP.W / 2.3;
        var card = $(e);
        if (card.offset().left < APP.W / 2) {
          dir = -APP.W / 2.3
        }
        card.transition({
          x: dir,
          rotate: _.random(-10, 10),
          delay: _.random(300, 600)
        }, 'easeInOutExpo')
      })
      $('.grid-label').transition({
        opacity: 0
      })
    }

    function resetCards() {
      $('.fe-card').each(function(i, e) {
        var card = $(e);
        card.transition({
          x: 0,
          rotate: 0,
          delay: _.random(300, 600)
        }, 'easeInOutExpo')
      })
      $('.grid-label').transition({
        opacity: 1,
        delay: _.random(300, 600)
      })
    }

    function showcase() {
      if (!_.isEmpty(queue) && (APP.forceAdvCounter < APP.advLimit || queue.length >= APP.queueLimit)) {
        var showcase = _.take(queue, 1)[0];
        queue = _.without(queue, showcase)
        _.dropRight(cards, 1);
        cards.unshift(showcase);
        separateCards();
        showcaseCard(showcase, true);
        if(APP.showAdv) APP.forceAdvCounter++;
      } else if((!_.isEmpty(queue) && APP.forceAdvCounter >= APP.advLimit) || (_.isEmpty(queue) && APP.showAdv)){
        gradientTransition();
        separateCards();
        showcaseAdv();
        APP.forceAdvCounter = 0;
      } else {
        var card = randElem(_.takeRight(cards, cards.length-2));
        separateCards();
        showcaseCard(card)
      }
    }

    function showcaseCard(c, newCard) {
      var card = createCard(c);
      showcase_container.append(card)
      card.css({
        scale: 0
      })
      card.find('.fe-card-image').on("load", function() {
        setCardOrientation(card)
        card.transition({
          scale: 1,
          delay: 300,
          complete: function() {
            if(newCard) APP.animations.in();
            setTimeout(function() {
              if(newCard){
                newCard = false;
                APP.animations.out();
                updateGrid();
              }
              clearCardShowcase(newCard);
            }, APP.timers.showcase)
          }
        }, 800, 'easeInOutExpo')
      })
      currentShowcase = card;
    }

    function clearCardShowcase(newCard) {
      if (currentShowcase) {
        var card = currentShowcase;
        if(newCard){
          card.transition({
            x: -APP.W / 2,
            y: -APP.H / 2,
            opacity: 0,
            rotate: featured_container.children().first().css('rotate'),
            scale: .6,
            complete: function() {
              $(this).remove()
            }
          })
        } else {
          card.transition({
            scale: 0,
            complete: function() {
              $(this).remove()
            }
          })
        }
        resetCards();
      }
    }

    function showcaseAdv() {
      var adv = $('.fe-adv').eq(APP.advIndex);
      APP.advIndex++;
      if(APP.advIndex >= $('.filler-game-adv').length) APP.advIndex = 0;
      adv.css({
        display: 'block',
        scale: 0
      })
      adv.transition({
        scale: 1,
        delay: 300,
        complete: function() {
          setTimeout(clearAdvShowcase, APP.timers.showcase)
        }
      }, 500, 'easeInOutExpo')
      currentShowcase = adv
    }

    function clearAdvShowcase() {
      APP.showAdv = false;
      currentShowcase.transition({
        scale: 0,
        complete: function() {
          $(this).hide();
        }
      })
      gradientTransition(true)
      resetCards();
    }

    function gradientTransition(out) {
      if (!out) {
        $('.container:not(.showcase)').addClass('blue')
      } else {
        $('.container:not(.showcase)').removeClass('blue')
      }
    }

    function setCardOrientation(card) {
      var img = card.find('.fe-card-image');
      if (img.width() > img.height()/1.5) {
        card.addClass('wide')
      } else {
        card.addClass('tall')
      }
    }

    function showLoader(){
      featured_container.css('opacity', 0)
      grid_container.css('opacity', 0)
      showcase_container.append('<div class="loader inv"></div>')
    }

    function hideLoader(){
      featured_container.transition({
        opacity: 1
      })
      grid_container.transition({
        opacity: 1
      })
      showcase_container.find('.loader.inv').transition({
        opacity: 0,
        complete: showcase_container.find('.loader.inv').remove()
      });
      $('.grid-label').transition({
        opacity: 1
      })
    }

  }

  window.ImageGrid = ImageGrid;
})()