;(function ($) {
  window.APP = {}
  APP.grid = new ImageGrid();
  APP.animations = new Animations();

  APP.update = update;
  APP.advIndex = 0;
  APP.forceAdvCounter = 0;
  APP.advLimit = 2;
  APP.queueLimit = 5;
  APP.timers = {
    cycle: 10000,
    showcase: 5000,
    update: 20000,
    adv: 40000
  }

  $(document).ready(function () {
    console.log('ready')
    APP.W = $(window).width();
    APP.H = $(window).height();
    APP.grid.init()
    APP.grid.showLoader()
    $.getJSON('data/handle-blacklist.json', function(data){
      console.log('blacklist')
      APP.blacklist = data.blacklist
      loadData(function(items){
        console.log('items')
        init();
        APP.grid.create(items)
        APP.grid.hideLoader()
      });
    })
  })

  $( window ).resize(function() {
    APP.W = $(window).width();
    APP.H = $(window).height();
  });

  function init(){
    APP.cycle = setInterval(update, APP.timers.update)
    APP.advCycle = setInterval(enableAdv, APP.timers.adv)
    APP.animations.init();
  }

  function loadData(callback){
    var promises = [];

    var insta_promise = Q.defer();
    var twitter_promise = Q.defer();

    promises.push(insta_promise.promise, twitter_promise.promise);

    $.get('https://emiliobondioli.runkit.io/instagram-hashtag-scraper/branches/master', function(data){
      insta_promise.resolve(data.items);
    })

    $.get('https://emiliobondioli.runkit.io/twitter-hashtag-scraper/branches/master', function(data){
      twitter_promise.resolve(data.items);
    })

    Q.all(promises).then(function(results){
      console.log('API', results)
      var items = _.orderBy(results[0].concat(results[1]), 'date', 'desc');
      items = _.filter(items, function(i){
        return !_.includes(APP.blacklist, i.profile_handle)
      })
      console.log('callback')
      if(callback) {
        callback(items)
      }
    })
  }

  function enableAdv(){
    APP.showAdv = true;
  }

  function update(){
    loadData(function(items){
      APP.grid.update(items)
    })
  }

})(window.jQuery)
